body {
  background-color: rgb(19, 19, 19);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}
.negative {
  color: rgb(180, 28, 28) !important;
  fill: rgb(180, 28, 28) !important;
}
.positive {
  color: rgb(72, 134, 0) !important;
  fill: rgb(72, 134, 0) !important;
}

.BossName {
  font-size: 1.3em;
}

.alertWrapper {
  width: 80%;
  z-index: 2000;
  position: absolute;
  left: 10%;
  bottom: 2.6em;
}

/* width */
.playerScroller::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 0.25rem !important;
}

.playerScroller::-webkit-scrollbar {
  width: 12px;
  padding-right: 2em;
  background-color: rgb(30, 30, 30);
}

.playerScroller::-webkit-scrollbar-thumb {
  border-radius: 0.25rem !important;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #333;
}

.searchBar {
  background-color: rgb(30, 30, 30);
  border-color: rgb(22, 22, 22);
}
.searchBar:focus {
  background-color: rgb(32, 32, 32);
  border-color: rgb(22, 22, 22);
  box-shadow: 0 0 0 0.2rem rgba(108, 117, 125, 0.5);
}

.PlayerListItem:nth-child(even) {
  background-color: rgb(27, 27, 27);
}
.PlayerListItem:nth-child(odd) {
  background-color: rgb(30, 30, 30);
}
.BossHeader {
  cursor: pointer;
}
.BossHeader:hover {
  background-color: #2e3030 !important;
}
.LootHeader {
  cursor: pointer !important;
}
.LootHeader:hover {
  cursor: pointer !important;
  background-color: #303030 !important;
}
.LootList {
  background-color: rgb(40, 40, 40);
  width: 95%;
}
.LootName {
  cursor: pointer;
  font-size: 1.2em;
}
.LootImage {
  height: 48px;
  box-shadow: 0 0 6px rgba(163, 53, 238, 0.7);
}
.filterBtn {
  height: 2em;
  width: 2em;
  background-color: rgb(30, 30, 30);
  border-color: rgb(22, 22, 22);
  font-weight: 700;
  font-size: 1em;
}
.filterBtn:hover {
  background-color: rgb(30, 30, 30);
}
.filterBtn:active {
  background-color: rgb(30, 30, 30) !important;
}
.yeetGif {
  opacity: 0.4;
  -webkit-filter: grayscale(60%);
          filter: grayscale(60%);
}
.playerScroller {
  overflow-y: auto;
  overflow-x: hidden;
  background-color: rgb(30, 30, 30);
  max-height: 20em;
  padding: 0px;
}
.rbTooltip {
  width: 9em;
  position: absolute;
  top: -0.3em;
  margin-left: 2.5em;
  z-index: 4000;
  border: 1px solid rgb(22, 22, 22);
}
.rbttText {
  font-size: 0.9em;
  font-weight: 500;
}
.rbttText2 {
  font-size: 0.8em;
}
.psImage {
  height: 48px;
}
.PlayerRank {
  color: #444;
  width: 2em;
  text-align: center;
  opacity: 0.5;
}
.PlayerImage {
  width: 40px;
}
.PlayerName {
  min-width: 6em;
  font-size: 1.1em;
  margin-right: 0.5em;
}
.PlayerSim {
  font-size: 1.15rem;
}
.percentText {
  font-size: 1.5rem;
}
.rank1 {
  color: rgb(255, 196, 0);
}
.rank2 {
  color: rgb(211, 211, 211);
}
.rank3 {
  color: rgb(126, 77, 31);
}
.graphSVG {
  vertical-align: middle;
}
.svgLine1 {
  fill: rgb(16, 16, 16);
}
.svgLine2 {
  fill: rgb(72, 134, 0);
}
.svgCircle {
  fill: rgb(211, 95, 0);
}
.text-success {
  color: rgb(72, 134, 0) !important;
}
.text-light {
  color: rgb(220, 220, 220) !important;
}
.bg-dark {
  background-color: #222 !important;
}

.armoryLink {
  text-decoration: none !important;
}
.armoryLink:hover img {
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.4);
}
.armoryLink:hover h5 {
  text-shadow: 0 0 1px rgba(255, 255, 255, 0.4);
}
.itemText {
  color: #a335ee !important;
}
.lootLink:hover {
  color: #a335ee !important;
  border-bottom-color: #a335ee;
}
.class1 {
  color: #c79c6e !important;
}
.class2 {
  color: #f58cba !important;
}
.class3 {
  color: #abd473 !important;
}
.class4 {
  color: #fff569 !important;
}
.class5 {
  color: #ffffff !important;
}
.class6 {
  color: #c41f3b !important;
}
.class7 {
  color: #0070de !important;
}
.class8 {
  color: #40c7eb !important;
}
.class9 {
  color: #8787ed !important;
}
.class10 {
  color: #00ff96 !important;
}
.class11 {
  color: #ff7d0a !important;
}
.class12 {
  color: #a330c9 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.wowIco {
  width: 1.45em;
  height: auto;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.7;
  margin: auto;
}
.wowBtn {
  display: flex;
  align-content: center;
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 0.5em;
  padding-right: 0.5em;
}
.rbIco {
  height: 30px;
  -webkit-filter: grayscale(100%) brightness(40%);
          filter: grayscale(100%) brightness(40%);
}

.rbIco:hover {
  -webkit-filter: grayscale(0%) brightness(80%);
          filter: grayscale(0%) brightness(80%);
}
.wowHeadIco {
  -webkit-filter: grayscale(100%) brightness(50%);
          filter: grayscale(100%) brightness(50%);
  opacity: 0.5;
}
.wowHeadIco:hover {
  -webkit-filter: grayscale(0%) brightness(70%);
          filter: grayscale(0%) brightness(70%);
  opacity: 1;
}

.roleIco {
  -webkit-filter: grayscale(40%) brightness(80%);
          filter: grayscale(40%) brightness(80%);
}
.roleIco:hover {
  -webkit-filter: grayscale(0%) brightness(80%);
          filter: grayscale(0%) brightness(80%);
}

.PlayerModalImage {
  width: 40px;
}
.modal-header {
  border-color: #333;
}
.modal-footer {
  border-color: #333;
}

.container-fluid {
  padding: 0px;
}
.psGuild {
  text-transform: capitalize;
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 40em;
  }
}
@media (min-width: 576px) {
  .container-fluid {
    max-width: 4000px;
    padding: 0px;
  }
  .rbTooltip {
    display: none;
  }
}
@media (min-width: 768px) {
  .container-fluid {
    max-width: 4000px;
    padding: 0px;
  }
  .rbTooltip {
    display: block;
  }
}
@media (min-width: 992px) {
  .container-fluid {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container-fluid {
    max-width: 1140px;
  }
}

.tooltipWrapper > .rbTooltip {
  display: none;
}

.tooltipWrapper:hover > .rbTooltip {
  display: block;
}

